.mainblog {
  padding-top: 200px;
  padding-bottom: 150px;
  background-color: var(--colorWhite);
  &__wrap {
    position: relative;
  }

  &__title {
    display: inline-block;
  }

  &__all {
    display: inline-block;
    margin-left: 70px;
    font-size: 18px;
    color: var(--colorMain);
    text-decoration: underline;
    position: relative;
    transition: all 0.3s ease 0s;
    &:hover {
      color: #000;
    }
    &:after {
      content: "";
      position: absolute;
      right: -15px;
      top: calc(50% + 2px);
      transform: translateY(-50%) rotate(-45deg);
      width: 6px;
      height: 6px;
      background-color: transparent;
      border-right: 1px solid var(--colorMain);
      border-bottom: 1px solid var(--colorMain);
    }
  }

  &__slider {
    margin-top: 85px;
  }

  &__slide {
  }
  .swiper-wrapper {
    width: 100%;
    height: 100%;
  }
  .swiper-button-prev {
    background: transparent url("../img/arrow-left-slide-newsblog.png") 0 0 no-repeat;
    color: transparent;
    left: 50px;
    width: 48px;
    height: 48px;
    top: 35px;
    right: 300px;
    left: auto;
  }
  .swiper-button-next {
    background: transparent url("../img/arrow-right-slide-newsblog.png") 0 0 no-repeat;
    color: transparent;
    right: 50px;
    width: 48px;
    height: 48px;
    top: 35px;
    right: 230px;
  }
}

.slide-item {
  width: 362px !important;
  img {
    display: block;
    height: 100%;
    object-fit: cover;
    margin: 0 auto;
  }
  &__img {
    img {
      border-radius: 20px;
      border-bottom-left-radius: 0px;
    }
  }

  &__name {
    font-size: 18px;
    line-height: 28px;
    text-transform: uppercase;
    font-weight: 700;
    margin-top: 35px;
  }

  &__text {
    margin-top: 25px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
  }
}
