.item-worker {
  margin-bottom: 120px;
  &:last-child {
    margin-bottom: 0;
    padding-bottom: 320px;
  }
  &:nth-child(3),
  &:nth-child(4) {
    background-color: var(--colorWhite);
    position: relative;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 100%;
      width: 100%;
      height: 360px;
      background-color: var(--colorWhite);
      z-index: -1;
    }
  }
  &:nth-child(3) {
    &:before {
      background: linear-gradient(to right bottom, transparent 50%, var(--colorWhite) 50%);
    }
  }
  &:nth-child(odd) {
    .item-worker {
      &__descr {
        padding-left: 145px;
      }
    }
  }
  &:nth-child(even) {
    .item-worker {
      &__descr {
        order: -1;
        padding-right: 145px;
      }
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
  }

  &__img {
    flex-basis: 50%;
  }

  &__descr {
    flex-basis: 50%;
  }

  &__name {
    font-size: 30px;
    line-height: 30px;
    font-weight: 700;
    text-transform: uppercase;
  }

  &__social {
    align-self: flex-end;

    > ul {
      list-style: none;
      display: flex;
      padding-left: 0;
      > li {
        width: 25px;
        height: 25px;
        margin-right: 7px;
        img {
          width: 25px;
          height: 25px;
          transition: all 0.3s ease 0s;
          border-radius: 50%;
          &:hover {
            //transform: scale(1.03);
            box-shadow: 1px 1px 7px rgba(#6d7479, 0.5), -1px -1px 7px rgba(#6d7479, 0.5);
          }
        }
      }
    }
  }

  &__text {
    font-size: 16px;
    line-height: 30px;
    max-width: 560px;
    margin-top: 25px;
  }
}
.uusi-banner {
  padding-top: 30px;
  padding-bottom: 30px;
  .banner {
    transform: translateY(-150px);
    &__text {
      p:nth-child(1) {
        span {
          &:after {
            width: 0;
          }
        }
      }
    }
  }
}
