.winners {
  padding-top: 150px;
  padding-bottom: 400px;
  @media (max-width: 992px) {
    padding-top: 85px;
  }
  @media (max-width: 576px) {
    padding-top: 45px;
  }
  &__wrap {
  }

  &__title {
    font-size: 36px;
    line-height: 70px;
    font-weight: 900;
    text-align: center;
  }

  &__slider {
    margin-top: 80px;
    @media (max-width: 992px) {
      margin-top: 50px;
    }
    @media (max-width: 992px) {
      margin-top: 30px;
    }
  }
  .swiper {
    padding-left: 150px;
    padding-right: 150px;
    @media (max-width: 1200px) {
      padding-left: 0px;
      padding-right: 0px;
    }
  }
  .swiper-slide-prev {
    opacity: 0.3;
  }

  .swiper-button-prev {
    left: 70px !important;
    top: 35%;
    transform: translateY(-50%);
    bottom: auto;
    width: 48px !important;
    height: 48px !important;
    border: 2px solid var(--colorMain);
    border-radius: 50%;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    opacity: 0.5;
    &:after {
      color: var(--colorMain) !important;
      width: 12px !important;
      height: 15px !important;
    }
    @media (max-width: 1860px) {
      //top: 250px !important;
      //left: 165px !important;
    }
    @media (max-width: 1400px) {
      top: 25%;
      //top: 250px !important;
      //right: auto;
      //left: 240px !important;
    }
    @media (max-width: 1200px) {
      left: 10px !important;
      top: 35%;
      width: 35px !important;
      height: 35px !important;
      //top: 250px !important;
      //right: auto;
      //left: 240px !important;
    }
    @media (max-width: 992px) {
      top: 30% !important;
    }
    @media (max-width: 768px) {
      display: none !important;
    }
  }
  .swiper-button-next {
    z-index: 1000;
    right: 70px !important;
    top: 35%;
    transform: translateY(-50%);
    bottom: auto;
    width: 48px !important;
    height: 48px !important;
    border: 2px solid var(--colorMain);
    border-radius: 50%;
    opacity: 0.5;
    &:after {
      color: var(--colorMain) !important;
      width: 5px !important;
      height: 15px !important;
    }
    @media (max-width: 1860px) {
      //top: 250px !important;
      //right: auto;
      //left: 240px !important;
    }
    @media (max-width: 1400px) {
      top: 25%;
      right: 85px !important;
      //top: 250px !important;
      //right: auto;
      //left: 240px !important;
    }
    @media (max-width: 1200px) {
      right: 10px !important;
      top: 35%;
      width: 35px !important;
      height: 35px !important;
      //top: 250px !important;
      //right: auto;
      //left: 240px !important;
      &:after {
        width: 8px !important;
      }
    }
    @media (max-width: 992px) {
      top: 30% !important;
    }
    @media (max-width: 768px) {
      display: none !important;
    }
  }
}

.slide-winners {
  &__item {
  }
  &__descr {
    max-width: 90%;
    @media (max-width: 576px) {
      max-width: 100%;
    }
  }
  &__img {
    max-width: 480px;
    max-height: 360px;
  }

  &__name {
    margin-top: 30px;
    font-size: 18px;
    line-height: 28px;
    font-weight: 700;
    text-transform: uppercase;
  }

  &__text {
    margin-top: 25px;
    font-size: 16px;
    line-height: 24px;
  }
}
