.store {
  padding-top: 200px;
  &__banner {
  }

  .banner-yell {
    &__descr {
    }

    &__title {
    }

    &__text {
      position: relative;
      z-index: 1;
    }

    &__img {
      img {
        top: auto;
        bottom: 0;
        width: auto;
        height: 100%;
        @media (max-width: 992px) {
        }
      }
    }
  }
}

.store-block {
  margin-top: 110px;
  padding-bottom: 130px;

  &__wrap {
    display: grid;
    grid-template-columns: repeat(3, 490px);
    grid-template-rows: repeat(3, 680px);
    gap: 30px;
  }

  &__item {
    margin-top: 70px;
  }

  &__img {
    background-color: var(--colorWhite);
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    overflow: hidden;
    img {
    }
  }

  &__name {
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;
    text-transform: uppercase;
    max-width: 80%;
    margin-top: 40px;
  }

  &__button {
    text-align: center;
    margin-top: 115px;
    @media (max-width: 1200px) {
      margin-top: 80px;
    }
    @media (max-width: 992px) {
      margin-top: 40px;
    }
    @media (max-width: 577px) {
      margin-top: 15px;
    }
    button {
      background-color: transparent;
      border: 2px solid var(--colorYellow);
    }
  }
}
