:root {
  // base
  --font-family: "DINPro";
  --content-width: 1525px;
  --container-offset: 25px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --swiper-navigation-size: 15px;

  // colors
  --colorMain: #323e48;
  --colorYellow: #ffd145;
  --colorWhite: #ffffff;
  --colorBg: #edf1f7;
}
