.header {
  width: 100%;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  transition: all 0.3s ease 0s;
  &.backgroundWhite {
    background-color: rgba(255, 255, 255, 1);
    padding: 10px 0;
    border-bottom: 1px solid rgba(#323e48, 0.3);
  }
  //left: 50%;
  //transform: translateX(-50%);
  //top: 0;

  padding: 32px 0;
  &__wrap {
    .burger {
      position: absolute;
      display: none;
      width: 40px;
      height: 20px;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      background-color: transparent;
      transition: all 0.3s ease 0s;
      &:after,
      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0px;
        width: 100%;
        height: 2px;
        background-color: var(--colorMain);
        transition: all 0.3s ease 0s;
      }
      &:before {
        top: auto;
        bottom: 0px;
      }
      span {
        position: absolute;
        width: 100%;
        height: 2px;
        display: inline-block;
        background-color: var(--colorMain);
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        transition: all 0.3s ease 0s;
      }
      &.open {
        &:after {
          transform: rotate(-45deg);
          top: 9px;
        }
        &:before {
          transform: rotate(45deg);
          bottom: 9px;
        }
      }
      &.open {
        span {
          transform: scale(0);
        }
      }
    }
  }
}

.nav-menu {
  > ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    > li {
      list-style: none;
      margin: 0 45px;
      &:nth-child(4) {
        a {
          &:after {
            width: 0%;
          }
        }
      }
      > a {
        color: var(--colorMain);
        font-size: 18px;
        text-transform: uppercase;
        position: relative;
        &.active {
          &:hover:after {
            transform: scale(0);
          }
          &:before {
            @include circle;
          }
        }

        &:after {
          content: "";
          position: absolute;
          width: 100%;
          height: 2px;
          background-color: var(--colorMain);
          bottom: -3px;
          left: 0;
          transform: scale(0);
          transition: all 0.3s ease 0s;
        }
        &:hover:after {
          transform: scale(1);
        }
      }
    }
  }
}
