.learning {
  padding-top: 200px;
  &__wrap {
    padding-left: 110px;
    padding-right: 110px;
  }

  &__top {
  }

  &__bottom {
    background-color: var(--colorWhite);
  }

  .banner-yell {
    background-color: var(--colorWhite);
    &__descr {
    }

    &__title {
    }

    &__text {
    }

    &__img {
      img {
        top: auto;
        bottom: 0;
        right: 30px;
      }
    }
  }

  .banner {
    &__img {
    }

    &__text {
    }
  }
}

.top-learning {
  &__text {
    font-size: 18px;
    line-height: 36px;
    margin-top: 100px;
  }

  &__img {
    text-align: center;
    margin-top: 40px;
  }

  &__descr {
    margin-top: 90px;
  }

  &__name {
    font-size: 30px;
    line-height: 40px;
  }

  &__list {
    margin-top: 30px;
    margin-bottom: 100px;
    > ul {
      list-style: none;
      padding: 0;
      margin: 0;
      > li {
        padding-left: 40px;
        background: url("../img/icons/checkmark-circle-2.svg");
        background-size: 20px 20px;
        background-position: left -1px;
        background-repeat: no-repeat;
        margin-bottom: 15px;
        font-size: 18px;
        line-height: 24px;
      }
    }
  }

  &__banner {
  }
}
.bottom-learning {
  padding-bottom: 135px;
  &__descr {
  }

  &__list {
    margin-top: 30px;
    margin-bottom: 100px;
    > ul {
      list-style: none;
      padding: 0;
      margin: 0;
      > li {
        padding-left: 40px;
        background: url("../img/icons/checkmark-circle-2.svg");
        background-size: 20px 20px;
        background-position: left -1px;
        background-repeat: no-repeat;
        margin-bottom: 15px;
        font-size: 18px;
        line-height: 24px;
      }
    }
  }

  &__name {
    font-size: 30px;
    line-height: 40px;
    padding-top: 100px;
  }

  &__text {
    p {
      font-size: 30px;
      line-height: 40px;
      &:last-child {
        font-size: 40px;
        font-weight: 700;
      }
    }
  }

  &__banner {
    margin-top: 140px;
  }
}

.accreditation {
  padding-bottom: 95px;
  position: relative;
  @media (max-width: 1200px) {
    padding-bottom: 50px;
  }
  @media (max-width: 577px) {
    padding-bottom: 20px;
  }
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 170px;
    background: linear-gradient(to right bottom, var(--colorBg) 50%, transparent 51%);
    z-index: 1;
    @media (max-width: 1200px) {
      height: 100px;
    }
    @media (max-width: 577px) {
      height: 50px;
    }
  }
  &__banner {
    img {
      max-height: 475px;
      width: auto;
    }
  }
}

.accreditation {
  &__banner {
    img {
      max-height: 475px;
      width: auto;
      @media (max-width: 1456px) {
        max-height: 400px;
        width: auto;
      }
      @media (max-width: 992px) {
        max-height: 300px;
        width: auto;
      }
    }
  }
  .arts-huluku__img {
    margin-right: 0px !important;
    @media (max-width: 768px) {
      margin-bottom: 40px !important;
    }
  }
  &:nth-child(1) {
    .arts-huluku__title {
      margin-top: 60px !important;
      @media (max-width: 1200px) {
        margin-top: 0 !important;
      }
    }
  }
  &:nth-child(odd) {
    .arts-huluku__descr {
      order: -1;
      @media (max-width: 768px) {
        order: 0;
      }
    }
  }
}
