.another {
  padding-top: 120px;
  padding-bottom: 100px;
  background-color: var(--colorMain);
  color: var(--colorWhite);
  text-align: center;
  &__wrap {
  }

  &__title {
    font-size: 36px;
    line-height: 44px;
    font-weight: 900;
  }

  &__text {
    font-size: 20px;
    line-height: 44px;
    max-width: 1330px;
    margin: 50px auto 0 auto;
    p:nth-child(1) {
      margin-bottom: 40px;
    }
  }

  &__banner {
    margin-top: 130px;
    .banner-yell {
      &__descr {
        padding-left: 0;
        text-align: left;
        color: var(--colorMain);
      }

      &__title {
        font-size: 80px;
        font-weight: 900;
        line-height: 90px;
        margin-top: 20px;
      }

      &__text {
        margin-top: 20px;
        font-size: 60px;
        line-height: 70px;
        text-transform: uppercase;
        p {
          //margin-top: 0;
        }
      }

      &__img {
        order: -1;
        img {
          top: auto;
          left: 10px;
          bottom: 0;
        }
      }
    }
  }
}
