.artshuluku {
}
.arts-huluku {
  &:nth-child(3),
  &:nth-child(1) {
    .arts-huluku__img {
      margin-right: 100px;
    }
  }
  padding-top: 230px;
  padding-bottom: 50px;
  &:nth-child(odd) {
    background-color: var(--colorWhite);
    position: relative;

    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 170px;
      background: linear-gradient(to right bottom, var(--colorWhite) 50%, #edf1f7 51%, transparent 52%);
      z-index: 1;
    }
  }
  &:nth-child(even) {
    background-color: #fafbfd;
    position: relative;
    .arts-huluku__descr {
      order: -1;
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 170px;
      background: linear-gradient(to right bottom, #fafbfd 50%, #edf1f7 51%, transparent 52%);
      z-index: 1;
    }
  }

  &__wrap {
  }

  &__items {
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__img {
  }

  &__descr {
    max-width: 650px;
  }

  &__title {
    font-weight: 900;
  }

  &__text {
    margin-top: 45px;
    font-size: 20px;
    line-height: 30px;
  }

  &__btn {
    margin-top: 50px;
  }
  &__banner {
    padding-top: 250px;
    padding-bottom: 200px;
  }
}
