.news {
  padding-top: 200px;
  padding-bottom: 100px;
  &__title {
  }

  &__nav {
    margin-top: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > ul {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
      > li {
        margin-right: 23px;
        a {
          display: inline-block;
          font-size: 18px;
          padding: 15px 20px;
          color: var(--colorMain);
          text-transform: uppercase;
          border-radius: 10px;
          background-color: var(--colorWhite);
          transition: all 0.3s ease 0s;
          &:hover {
            background-color: var(--colorYellow);
          }
          &.active {
            background-color: var(--colorYellow);
          }
        }
      }
    }
  }

  &__search {
    display: flex;
    .search {
      margin-right: 20px;
      font-size: 18px;
      color: #a1a7ad;
      &.active {
        display: none;
      }
    }
    position: relative;
    cursor: pointer;
    button {
      appearance: none;
      background: 0 0;
      box-shadow: none;
      border: none;
      cursor: pointer;
      padding: 0;
      margin: 0;
      width: 18px;
      height: 18px;
      display: none;
      position: relative;
      z-index: 2;
    }

    input {
      width: 170px;
      padding: 10px 65px 10px 20px;
      box-shadow: 0 0 10px 0 #000;
      border: none;
      font-size: 12px;
      line-height: 16px;
      color: #000;
      border-radius: 22px;
      position: absolute;
      left: -137px;
      top: -9px;
      z-index: -1;
      opacity: 0;
      appearance: none;
    }
    &.active input {
      opacity: 1;
      z-index: 1;
    }
    &.active button {
      display: block;
    }
    &.active .icons__img {
      display: none;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(3, 1fr);
    justify-items: start;
    @media (max-width: 1350px) {
      grid-template-columns: 1fr;
      grid-template-rows: auto;
      justify-items: center;
    }
  }

  &__item {
    margin-top: 75px;
    &:nth-child(4) {
      justify-self: end;
      @media (max-width: 1350px) {
        justify-self: center;
      }
    }
  }
  &__button {
    text-align: center;
    margin-top: 115px;
    @media (max-width: 1200px) {
      margin-top: 80px;
    }
    @media (max-width: 992px) {
      margin-top: 40px;
    }
    @media (max-width: 577px) {
      margin-top: 15px;
    }
    button {
      background-color: transparent;
      border: 2px solid var(--colorYellow);
    }
  }
}

.icons {
  &__img {
  }
}
.item-news {
  max-width: 625px;
  &_width {
    max-width: 750px;
    /* @media (max-width: 1550px) {
      max-width: 625px;
    } */
  }
  &__img {
  }

  &__name {
    font-size: 24px;
    line-height: 36px;
    text-transform: uppercase;
    margin-top: 40px;
    font-weight: 700;
  }

  &__text {
    font-size: 18px;
    line-height: 30px;
    margin-top: 25px;
  }

  &__info {
    font-size: 14px;
    line-height: 30px;
    color: #717171;
    margin-top: 20px;
  }

  &__button {
  }

  &__btn {
  }
}
