.perito-promo {
  padding-top: 200px;
  &__wrap {
  }

  &__banner {
  }

  &__descr {
    margin-top: 170px;
    text-align: center;
  }

  &__title {
    font-size: 36px;
    line-height: 70px;
    font-weight: 900;
  }

  &__text {
    font-size: 18px;
    line-height: 30px;
    max-width: 1367px;
    margin: 0 auto;
    p {
      margin-bottom: 40px;
    }
  }

  &__btn {
    margin-top: 40px;
    background-color: transparent;
    border: 2px solid var(--colorYellow);
    margin-bottom: 30px;
  }

  &__images {
    transform: translateY(90px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }

  &__image {
  }

  .banner-yell {
    &__descr {
      padding-left: 100px;
    }

    &__title {
      font-weight: 900;
    }

    &__text {
      font-size: 20px;
      line-height: 30px;
      max-width: 565px;
    }

    &__img {
      img {
        top: auto;
        bottom: 0;
        right: -15px;
      }
    }
    &__btn {
      background-color: var(--colorWhite);
      margin-top: 30px;
    }
  }
}
