.uusi-promo {
  padding-top: 200px;
  &__wrap {
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 125px 100px 230px;
    background-color: var(--colorWhite);
    border-radius: 20px;
  }

  &__title {
    color: var(--colorYellow);
    font-weight: 900;
    white-space: nowrap;
    margin-right: 100px;
  }

  &__subtitle {
    font-size: 36px;
    line-height: 44px;
  }

  &__bottom {
    text-align: center;
    transform: translateY(-180px);
  }

  &__img {
    margin-bottom: 100px;
  }

  &__text {
    max-width: 1000px;
    margin: 0 auto;
    font-size: 20px;
    line-height: 36px;
  }
}
