.huluku {
  padding-top: 200px;
  &__top {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 102%;
      width: 100%;
      height: 480px;
      background: linear-gradient(to right bottom, var(--colorBg) 50%, transparent 50%);
      z-index: 1;
    }
  }

  &__bottom {
  }
}

.top-huluku {
  &__banner {
    margin-bottom: 140px;
  }

  &__text {
    max-width: 1020px;
    margin: 0 auto;
    font-size: 20px;
    line-height: 36px;
  }
}
.bottom-huluku {
  padding-bottom: 64px;
  background-color: #313d47;
  text-align: center;
  padding-top: 250px;
  &__img {
    transform: translateY(-180px);
    position: relative;
    z-index: 1;
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
      width: 930px;
      height: 930px;
      background-color: rgba(255, 255, 255, 0.2);
      border-radius: 50%;
    }
  }
  &__text {
    font-size: 24px;
    line-height: 44px;
    color: var(--colorWhite);
    transform: translateY(-150px);
    max-width: 1300px;
    margin: 0 auto;
  }

  &__button {
    transform: translateY(-70px);
    display: inline-flex;
    flex-direction: column;
    align-items: flex-end;
    span {
      margin-top: 15px;
    }
  }

  &__btn {
    padding-left: 100px;
    padding-right: 100px;
    &:hover {
      &:hover {
        box-shadow: 1px 1px 10px rgba(255, 255, 255, 0.2), -1px -1px 10px rgba(255, 255, 255, 0.2);
      }
    }
  }
}
