.organisation {
  padding-top: 210px;
  padding-bottom: 150px;
  position: relative;
  overflow: hidden;
  &:before {
    content: "";
    position: absolute;
    width: 670px;
    height: 670px;
    background-color: #eceab8;
    border-radius: 50%;
    z-index: -1;
    top: 65%;
    transform: translate(-56%, -50%);
    @media (max-width: 1553px) {
      width: 0;
      height: 0;
    }
  }
  &:after {
    content: "";
    position: absolute;
    width: 310px;
    height: 310px;
    background-color: #eceab8;
    border-radius: 50%;
    right: 0px;
    left: auto;
    bottom: 10%;
    transform: translateX(100px);
    z-index: -1;
    @media (max-width: 1553px) {
      width: 0;
      height: 0;
    }
  }

  &__wrap {
  }

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    color: var(--colorYellow);
  }

  &__subtitle {
    margin-top: 40px;
    text-transform: uppercase;
    font-size: 30px;
    font-weight: 700;
    line-height: 40px;
  }

  &__text {
    font-size: 20px;
    line-height: 36px;
  }

  &__bottom {
    margin-top: 20px;
  }
}

.top-organisation {
  &__left {
    flex-basis: 55%;
  }

  &__right {
    flex-basis: 45%;
    padding-left: 100px;
  }
}

.bottom-organisation {
  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  &__img-wr {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 278px;
  }
  &__item {
    width: 360px;
    height: 460px;
    background-color: var(--colorWhite);
    border-radius: 20px;
    text-align: center;
    padding-top: 30px;
    margin-left: 75px;
    margin-right: 98px;
    margin-top: 130px;
    &:first-child {
      margin-left: 0;
    }
  }

  &__img {
  }

  &__descr {
    width: 100%;
    height: 135px;
    padding: 0 25px;
    background-color: var(--colorYellow);
    border-radius: 20px;
    transform: translate(30px, 60px);
    text-align: left;
    font-size: 15px;
    line-height: 28px;
    display: flex;
    align-items: center;
  }
}
