.howtoenter {
  padding-top: 240px;
  padding-bottom: 140px;
  background-blend-mode: multiply;
  background: url("../img/prize-img-5.png") right bottom / cover no-repeat;
  &__wrap {
  }

  &__descr {
    display: flex;
  }

  &__title {
    color: var(--colorYellow);
    padding-right: 200px;
  }

  &__list {
    color: var(--colorWhite);
    max-width: 911px;
    p {
      font-size: 18px;
      line-height: 30px;
      margin-bottom: 25px;
    }
    p:first-child {
      margin-top: 0;
    }
  }

  &__text {
    font-size: 30px;
    line-height: 48px;
    font-weight: 900;
    margin-top: 100px;
    max-width: 1170px;
    color: var(--colorWhite);
  }

  &__button {
    margin-top: 100px;
    display: inline-flex;
    flex-direction: column;
    align-items: flex-end;
    span {
      margin-top: 15px;
    }
  }

  &__btn {
    padding-right: 100px;
    padding-left: 100px;
  }

  .label {
  }
}
