.footer {
  background-color: var(--colorMain);
  padding-top: 45px;
  padding-bottom: 50px;
  color: var(--colorWhite);
  &__wrap {
    display: flex;
  }

  &__left {
    flex: 1 0 50%;
  }

  &__right {
    flex: 1 0 50%;
  }
}

.left-footer {
  &__logowrap {
    display: flex;
    align-items: center;
  }

  &__logo {
  }

  &__contacts {
    margin-left: 50px;
    a {
      display: block;
      font-size: 16px;
      line-height: 24px;
      color: var(--colorWhite);
    }
    a:last-child:hover {
      text-decoration: underline;
    }
  }

  &__text {
    margin-top: 35px;
    font-size: 12px;
    line-height: 18px;
    p {
      max-width: 455px;
    }
  }
}
.right-footer {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex-wrap: wrap;
  &__menu {
    > ul {
      list-style: none;
      display: flex;
      justify-content: space-between;
      > li {
        a {
          font-size: 16px;
          text-transform: uppercase;
          color: var(--colorWhite);
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }

  &__social {
    align-self: flex-end;

    > ul {
      list-style: none;
      display: flex;
      > li {
        width: 25px;
        height: 25px;
        margin-left: 7px;
        img {
          transition: all 0.3s ease 0s;
          border-radius: 50%;
          &:hover {
            //transform: scale(1.03);
            box-shadow: 1px 1px 7px rgba(#6d7479, 0.5), -1px -1px 7px rgba(#6d7479, 0.5);
          }
        }
      }
    }
  }
}
.copyright {
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  line-height: 18px;
  color: #6d7479;
  &__left {
  }

  &__right {
  }
}
