.mission {
  padding-top: 150px;
  &__wrap {
  }

  &__top {
    display: flex;
  }

  &__bottom {
  }
}

.top-mission {
  &__title {
    font-size: 80px;
    font-weight: 900;
  }

  &__btn {
    margin-top: 50px;
  }
  &__left {
    padding-right: 210px;
  }
  &__right {
    > ul {
      list-style: none;
      margin: 0;
      margin-top: 15px;
      padding: 0;
      > li {
        font-size: 20px;
        line-height: 30px;
        padding-left: 46px;
        margin-bottom: 30px;
        background-image: url("../img/icons/checkmark-circle-2.svg");
        background-repeat: no-repeat;
        background-position: left 6px;
        background-size: 24px 24px;
      }
    }
  }
}

.bottom-mission {
  &__banner {
    margin-top: 102px;
  }
}
