.anthology {
  background-color: var(--colorWhite);
  padding-bottom: 200px;
  &__wrap {
  }

  &__banner {
    transform: translateY(-250px);
  }

  &__right {
  }

  &__btn {
    background-color: var(--colorWhite);
  }

  &__content {
    margin-top: -50px;
  }
}

.banner-ant {
  padding: 85px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: url("../img/prize-img-7.png") center center / cover no-repeat;
  border-radius: 30px;
  &__left {
  }

  &__title {
    color: var(--colorYellow);
  }

  &__text {
    margin-top: 45px;
    max-width: 875px;
    color: var(--colorWhite);
    font-size: 18px;
    line-height: 30px;
  }
}

.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__img {
    flex: 0 1 50%;
  }

  &__descr {
    flex: 0 1 50%;
    padding-left: 30px;
  }

  &__title {
  }

  &__text {
    margin-top: 45px;
    font-size: 18px;
    line-height: 30px;
    p {
      margin-bottom: 25px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
