.arts-promo {
  padding-top: 140px;
  padding-bottom: 95px;
  position: relative;
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 170px;
    background: linear-gradient(to right bottom, var(--colorBg) 50%, transparent 51%);
    z-index: 1;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__descr {
  }

  &__title {
    font-weight: 900;
    line-height: 70px;
    color: var(--colorYellow);
    span {
      color: var(--colorMain);
      font-size: 100px;
      line-height: 100px;
      font-weight: 900;
    }
  }

  &__text {
    margin-top: 40px;
    max-width: 410px;
    font-size: 20px;
    line-height: 30px;
  }

  &__img {
  }
}
