html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-weight: 500;
  color: var(--colorMain);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  background-color: var(--colorBg);
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  &.lock {
    overflow: hidden;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  @media (max-width: 576px) {
    padding: 0 15px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn {
  color: var(--colorMain);
  font-size: 20px;
  line-height: 36px;
  padding: 20px 60px;
  background-color: var(--colorYellow);
  border-radius: 10px;
  text-transform: uppercase;
  font-weight: 500;
  transition: all 0.3s ease 0s;
  display: inline-block;
  &:hover {
    box-shadow: 1px 1px 7px rgba(0, 0, 0, 0.2);
  }
  @media (max-width: 992px) {
    padding: 15px 45px;
  }
  @media (max-width: 576px) {
    font-size: 16px;
    line-height: 28px;
    padding: 10px 30px !important;
  }
}
.title {
  text-transform: uppercase;
  font-size: 60px;
  font-weight: 700;
}
.banner {
  width: 100%;
  height: 420px;
  border-radius: 20px;
  background-color: var(--colorMain);
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__img {
    flex-basis: 60%;
    align-self: flex-end;
    img {
      position: absolute;
      top: -128px;
      left: -150px;
      @media (max-width: 1456px) {
        position: static;
        transform: translateY(4px);
      }
    }
  }

  &__text {
    flex-basis: 40%;
    max-width: 440px;
    transform: translateX(-150px);
    p {
      color: var(--colorWhite);
      font-weight: 700;
      font-size: 40px;
      text-transform: uppercase;
      line-height: 60px;
      span {
        position: relative;
        display: inline-block;
        margin-bottom: 7px;
        a {
          color: var(--colorYellow);
          font-size: 55px;
        }
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -3px;
          width: 100%;
          height: 4px;
          background-color: var(--colorYellow);
        }
      }
    }
  }
  @media (max-width: 1456px) {
    height: 350px;
    &__img {
      flex-basis: 50%;
    }
    &__text {
      flex-basis: 50%;
      max-width: 40%;
      margin: 0 auto;
      p {
        font-size: 37px;
        line-height: 37px;
        span {
          a {
            font-size: 50px;
          }
        }
      }
    }
  }
  @media (max-width: 1200px) {
    &__img {
      flex-basis: 50%;
    }
    &__text {
      flex-basis: 50%;
    }
    height: 300px;
    &__text {
      flex-basis: 50%;
      max-width: 70%;
      padding-left: 50px;
    }
  }
  @media (max-width: 992px) {
    &__img {
      display: none;
    }
    &__text {
      flex-basis: 100%;
      padding-left: 0;
      width: 100%;
      transform: translateX(0);
      text-align: center;
    }
  }
  @media (max-width: 768px) {
    &__text {
      p {
        font-size: 35px;
        line-height: 42px;
        span {
          a {
            font-size: 45px;
          }
        }
      }
    }
  }
  @media (max-width: 576px) {
    height: 250px;
    &__text {
      p {
        font-size: 24px;
        line-height: 30px;
        span {
          a {
            font-size: 30px;
          }
        }
      }
    }
  }
}

.label {
  color: #777777;
  font-size: 15px;
  line-height: 20px;
  span {
    text-transform: uppercase;
    display: inline-block;
    margin-left: 5px;
    font-weight: 700;
  }
  @media (max-width: 992px) {
    font-size: 13px;
    line-height: 13px;
  }
}
.banner-yell {
  width: 100%;
  height: 450px;
  background-color: var(--colorYellow);
  border-radius: 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__descr {
    flex-basis: 40%;
    padding-left: 120px;
  }

  &__title {
  }

  &__text {
    margin-top: 50px;
    font-size: 20px;
    line-height: 30px;
  }

  &__img {
    flex-basis: 60%;
    align-self: flex-end;
    img {
      position: absolute;
      top: -70px;
      right: 0px;
    }
  }
  &__btn {
    white-space: nowrap;
    @media (max-width: 992px) {
      padding: 10px 45px;
    }
    @media (max-width: 576px) {
      font-size: 16px;
      line-height: 28px;
      padding: 10px 30px !important;
    }
  }
  @media (max-width: 1456px) {
    height: 350px;
    &__img {
      flex-basis: 50%;
      img {
        width: 50%;
        top: -50px;
      }
    }
    &__descr {
      flex-basis: 50%;
      max-width: 40%;
      margin: 0 auto;
      padding-left: 0;
    }
  }
  @media (max-width: 1307px) {
    &__img {
      img {
        top: 0px;
      }
    }
  }
  @media (max-width: 1200px) {
    &__img {
      img {
        top: auto;
        bottom: 0;
      }
    }
  }
  @media (max-width: 992px) {
    &__text {
      margin-top: 15px;
      font-size: 17px;
      line-height: 25px;
    }
  }

  @media (max-width: 769px) {
    height: 280px;
    &__img {
      display: none;
      flex-basis: 0%;
    }
    &__descr {
      flex-basis: 100%;
      max-width: 100%;
      padding: 0 50px;
    }
  }

  @media (max-width: 577px) {
    height: 260px;
    &__descr {
      padding: 10px 30px;
    }
    &__text {
      font-size: 15px;
    }
  }
}
