    padding-top: 125px;
    padding-bottom: 325px;

    //background-size: 100%;
  }

  &__content {
    max-width: 722px;
    h1 {
      font-size: 48px;
      text-transform: auto;
      font-size: 48px;
      color: var(--colorMain);
      text-transform: uppercase;
